import { Button, Col, Dropdown, Form, Input, InputRef, Layout, Row, Space, Typography } from 'antd';
import { Footer, Header, Content } from 'antd/es/layout/layout';
import styles from './App.module.css';
import { useEffect, useRef, useState } from 'react';
import useGetLogin from './clients/login/useLogin';
import { jwtToJson, lsAutoScroll, lsRoom, lsSession } from './utils';
import Login from './components/Login';
import { socket } from './socket.io';

interface Message {
  id: string;
  room_id: string;
  user_id: string;
  username: string;
  message: string;
  created_at: string;
}

function App() {
  const inputMessage = useRef<InputRef>(null);
  const [autoScroll, setAutoScroll] = useState(lsAutoScroll.load() === 'true');
  const [newMessages, setNewMessages] = useState<Message[]>([]);
  const [isConnected, setIsConnected] = useState(socket.connected);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const [roomsEvents, setRoomsEvents] = useState<{ id: number, room_id: string }[]>([]);
  const [oldMessages, setOldMessages] = useState<Message[]>([]);
  const [currentRoom, setCurrentRoom] = useState<string>(lsRoom.load() || '');
  const [newMessage, setNewMessage] = useState<string>('');
  const [username, setUsername] = useState<string>(jwtToJson(lsSession.load() as string).username);
  useEffect(() => {
    function onConnect() {
      setIsConnected(true);
    }

    function onDisconnect() {
      setIsConnected(false);
    }

    function onRoomsEvent(value: any) {
      setRoomsEvents(value);
    }

    function onOldMessages(value: any) {
      setOldMessages(value);
      scrollToBottom();
      inputMessage.current?.focus();
    }

    function onNewMessages(value: any) {
      setNewMessages([value]);
      setOldMessages((prev) => {
        return [...prev, value];
      });
    }

    socket.on('connect', onConnect);
    socket.on('disconnect', onDisconnect);
    socket.on('rooms', onRoomsEvent);
    // oldMessages
    socket.on('oldMessages', onOldMessages);
    socket.on('message', onNewMessages);

    return () => {
      socket.off('connect', onConnect);
      socket.off('disconnect', onDisconnect);
      socket.off('rooms', onRoomsEvent);
      socket.off('oldMessages', onOldMessages);
      socket.off('message', onNewMessages);
    };
  }, []);

  // start view and selected room get old messages
  useEffect(() => {
    if (currentRoom) {
      // Emit joinRoom and wait for a response
      socket.emit('joinRoom', currentRoom);
    }
  }, [currentRoom]);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }

  const [isLogin, setIsLogin] = useState(lsSession.isLogin());
  const { getLogin } = useGetLogin();
  const [modalLoginOpen, setModalLoginOpen] = useState(false);
  const handleLoginToggle = () => {
    setModalLoginOpen(!modalLoginOpen);
  }
  const handleLogin = async (values: { username: string, password: string }) => {
    const res = await getLogin(values.username, values.password);
    if (res) {
      lsSession.save(res as string);
      handleLoginToggle();
      setIsLogin(true);
      // socket start 
      socket.auth = { token: res };
      socket.connect();
      getInitialSocketEvents();
      setUsername(values.username);
      lsAutoScroll.save('true');
      setAutoScroll(true);
    } else {
      console.log('Login failed');
    }
  }

  const getInitialSocketEvents = async () => {
    socket.emit('getRooms');
  }

  useEffect(() => {
    if (isLogin) {
      socket.auth = { token: lsSession.load() };
      socket.connect();
      getInitialSocketEvents();
    }
  }, [isLogin]);

  const handleLogout = () => {
    lsSession.clear();
    setIsLogin(false);
    lsRoom.clear();
    socket.disconnect();
    setIsConnected(false);
    lsAutoScroll.clear();
    setAutoScroll(false);
    setUsername('');
    setCurrentRoom('');
    setOldMessages([]);
  }

  const handleJoinRoom = async (dropItem: { key: string }) => {
    // leave current room
    socket.emit('leaveRoom', currentRoom);
    const room_id = roomsEvents.find((item) => `${item.id}` === dropItem.key)?.room_id;
    socket.emit('joinRoom', room_id);
    socket.emit('getOldMessages', room_id);
    setCurrentRoom(room_id as string);
    lsRoom.save(room_id as string);
    setTimeout(() => {
      inputMessage.current?.focus();
      console.log('focus');
    }, 0);
  }

  const handleSendMessage = async () => {
    socket.emit('message', currentRoom, newMessage);
    setNewMessage('');
  }

  useEffect(() => {
    // if some new message is created by me, scroll to bottom
    const isMyMessage = newMessages.some((item) => item.username === username);
    if (isMyMessage || autoScroll) {
      scrollToBottom();
    }
  }, [newMessages, username, autoScroll]);

  const handleToggleAutoScroll = () => {
    setAutoScroll((prev) => {
      lsAutoScroll.save(!prev ? 'true' : 'false');
      return !prev;
    });
  }

  return (
    <div className={styles.App}>
      <Layout>
        <Header className={styles.Header}>
          <Space>
            {isLogin ? <Button onClick={handleLogout}>
              {`Logout: ${username}`}
            </Button> : <Button onClick={handleLoginToggle}>Login</Button>}
            {isConnected ? <Dropdown
              menu={{
                onClick: handleJoinRoom,
                items: roomsEvents.map((item) => {
                  return {
                    key: item.id,
                    label: item.room_id,
                  }
                })
              }}
              placement="bottomLeft"
              arrow
            >
              <Button>{currentRoom ? `Room: ${currentRoom}` : 'Select Room'}</Button>
            </Dropdown> : <Button disabled style={{ backgroundColor: '#ffffff' }}>No Room</Button>}
            {isConnected && currentRoom ? (
              <Button onClick={handleToggleAutoScroll}>
              {autoScroll ? 'Auto Scroll is On' : 'Auto Scroll is Off'}
            </Button>
            ) : (
              <Button disabled style={{ backgroundColor: '#ffffff' }}>Auto Scroll</Button>
            )}
          </Space>
        </Header>
        <Content className={styles.Content} style={{ padding: '1rem 50px', overflowY: 'auto' }}>
          <Row>
            <Col span={24}>
              {oldMessages.map((item) => {
                const isMyMessage = item.username === username; // Reemplaza 'username' con tu nombre de usuario
                return (
                  <div
                    key={item.id}
                    style={{
                      textAlign: isMyMessage ? 'right' : 'left',
                    }}
                  >
                    <Typography.Title level={5} style={{ color: isMyMessage ? 'blue' : 'black' }}>
                      {item.username}
                    </Typography.Title>
                    <Typography.Text style={{ color: isMyMessage ? 'blue' : 'black' }}>
                      {item.message}
                    </Typography.Text>
                  </div>
                );
              })}
              <div ref={messagesEndRef} />
            </Col>
          </Row>
        </Content>
        <Footer className={styles.Footer}>
          <Form layout='inline' style={{ display: 'flex', width: '100%' }} onFinish={handleSendMessage}>
            {isConnected && currentRoom ? (
              <>
                <Form.Item style={{ flexGrow: 1 }}>
                  <Input
                    style={{ width: '100%' }}
                    value={newMessage}
                    onChange={(e) => setNewMessage(e.target.value)}
                    ref={inputMessage}
                  />
                </Form.Item>
                <Form.Item>
                  <Button htmlType="submit">Send</Button>
                </Form.Item>
              </>) : <Form.Item>
              <Button disabled>Send</Button>
            </Form.Item>}
          </Form>
        </Footer>
      </Layout>
      <Login visible={modalLoginOpen} setVisible={handleLoginToggle} onCreate={handleLogin} />
    </div>
  );
}

export default App;
