import { Modal, Form, Input, Button } from 'antd';

interface LoginProps {
  visible: boolean;
  setVisible: (visible: boolean) => void;
  onCreate: (values: { username: string, password: string }) => void;
}
const Login = ({ visible, setVisible, onCreate }: LoginProps) => {
  const [form] = Form.useForm();

  const handleCreate = () => {
    form
      .validateFields()
      .then((values) => {
        form.resetFields();
        onCreate(values);
      })
      .catch((info) => {
        console.log("Validate Failed:", info);
      });
  };
  return (
    <Modal
      open={visible}
      title="Create a new collection"
      okText="Ok"
      onCancel={() => {
        setVisible(false);
      }}
      onOk={handleCreate}
    >
      <Form form={form} layout="vertical" onFinish={handleCreate}>
        <Form.Item
          label="username"
          name="username"
          rules={[
            { required: true, message: "Please input the username of collection!" }
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label="password"
          name="password"
          rules={[
            { required: true, message: "Please input the password of collection!" }
          ]}
        >
          <Input />
        </Form.Item>
        <Button type="primary" htmlType="submit" style={{ display: 'none' }}>
          Create
        </Button>
      </Form>
    </Modal>
  );
}

export default Login;